import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {
  QueryVhsales,
  OrdinaryQuery,
  OrdinaryAlgorithm,
  OrdinaryQueryBusiness,
  OrdinaryComponent,
  FirebaseAuth,
  QueryVhenergy
} from "vhframeworks";

@Component({
  selector: 'app-reports',
  templateUrl: './reports.page.html',
  styleUrls: ['./reports.page.scss'],
})
export class ReportsPage implements OnInit {
  connect: boolean = false;
  iot_id:any = "CCC27BAE114C";


  subscription: any; 
  list_messages: Array<any> = [];

  listMessages: Array<any> = [];

  constructor(
    public router: Router, 
    public ordinaryQueryBusiness: OrdinaryQueryBusiness, 
    private queryVhenergy: QueryVhenergy,
    private ordinaryComponent: OrdinaryComponent
    ) { }

  ngOnInit() {
  }

  connectDevice(){
    if(this.iot_id !=""){
      this.connect = !this.connect;     
      if(this.connect){
        this.subscription = this.queryVhenergy.callbackLogValues(this.iot_id, 10).subscribe((data)=>{     
          this.list_messages = new Array();   
          if(data.size>0){
            for(let i=data.size-1; i>=0; i--){
              let val = data.docs[i].data();
              val.date = val.date.toDate().toString();              
              let value = JSON.stringify(val);
              this.list_messages.push(value);              
            }
            //console.log(this.list_messages);
          }         
        })
      }else{
        this.subscription.unsubscribe();
        this.list_messages = new Array();
      }
    }else{
      this.ordinaryComponent.showToast(1000, "Vui lòng nhập iotID")
    }

    /*this.queryVhenergy.callbackLogValues(messages => {
      if (!messages.empty) {
        this.listMessages = new Array(messages.size);
        let messagesReverse = messages.docs.reverse();
        this.listMessages = messagesReverse;
        console.log(this.listMessages)
        //this.scrollBottom();
      }
    },'CCC27BAE114C', 6)*/
    
    
  }

}
 